body {
  font-family: @font;
  font-size: 18px;
  line-height: 21px;
  font-weight: 400;
  width: 320px;
  text-align: center;
  margin: 0 auto;
  background-color: #e7e5e5;
}

.page__container {
  background-color: #ffffff;
}

@media (min-width: @tablet-width) {
  body {
    width: 768px;
  }
}

@media (min-width: @desktop-width) {
  body {
    width: 1200px;
  }
}
